import React, { useState, useEffect, useContext } from 'react';
import './Carousel.component.scss';
import { motion, AnimatePresence, useAnimation } from 'framer-motion';
import { AppContext } from '../../AppContext';
import ScrollDownIndicator from '../../components/ScrollDownIndicator/ScrollDownIndicator.component.jsx';
import CarouselInfo from '../../components/CarouselInfo/CarouselInfo.component.jsx';

export default function Carousel({ currentPos }) {
  const { isMD, isXXL, imageIndexObj, images } = useContext(AppContext);
  const [[page, direction], setPage] = useState([0, 0]);
  const [imageIndex, setImageIndex] = imageIndexObj;
  const animateHOne = useAnimation();

  const paginate = (newDirection) => {
    setPage([page + newDirection, newDirection]);
    setImageIndex(imageIndex + newDirection);
    if (imageIndex === images.length - 1) {
      setImageIndex(0);
    }
  };

  useEffect(() => {
    const next = document.getElementById('next');
    const interval = setInterval(() => next.click(), 5000);
    return () => {
      clearInterval(interval);
    };
  }, []);

  useEffect(() => {
    animateHOne.set({
      opacity: 0,
    });
    animateHOne.start({
      opacity: 1,
      transition: {
        delay: 0.5,
        duration: 0.5,
        ease: 'easeIn',
      },
    });
  }, [animateHOne]);

  return (
    <div
      className="carousel full-vh"
      id="carousel"
      style={{ opacity: currentPos }}
    >
      <div className="overlay" id="carousel-overlay"></div>
      <AnimatePresence custom={direction}>
        <motion.img
          key={page}
          custom={direction}
          className="carousel-image"
          // src={process.env.REACT_APP_PUBLIC_URL + `${images[imageIndex].link}`}
          src={images[imageIndex].link}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          transition={{
            opacity: { duration: 1 },
          }}
        />
      </AnimatePresence>
      <div
        className="next"
        onClick={() => paginate(1)}
        id="next"
        transition={{
          opacity: { duration: 1 },
        }}
      >
        <p>+</p>
      </div>
      <div className="prev" onClick={() => paginate(-1)}>
        <p>-</p>
      </div>
      <div className="carousel-text">
        <motion.h1
          animate={animateHOne}
          className={isXXL ? `display1` : isMD ? `display2` : ``}
        >
          our work
        </motion.h1>
      </div>
      <ScrollDownIndicator />
      <CarouselInfo
        description={images[imageIndex].service}
        id={images[imageIndex].id}
        length={images.length}
      />
    </div>
  );
}
