import React, { useContext } from 'react';
import './AboutPage.component.scss';
import Carousel from '../../components/Carousel/Carousel.component.jsx';
import Wrapper from '../../components/Wrapper/Wrapper.component.jsx';
import Container from '../../components/Container/Container.component.jsx';
import ContentArea from '../../components/ContentArea/ContentArea.component.jsx';
import Markdown from 'markdown-to-jsx';
import SectionH1 from '../../components/SectionH1/Sectionh1.component';
import Slider from '../../components/Slider/Slider.component.jsx';
import useFadeOutHook from '../../hooks/FadeOutHook/FadeOutHook.component';
import Footer from '../../components/Footer/Footer.component';

import { AppContext } from '../../AppContext';

export default function AboutPage() {
  const { certificationsData } = useContext(AppContext);
  const currentPos = useFadeOutHook();
  const copy = `ISR Technology provides services and solutions for business of all sizes. We are your single point of contact for all your IT needs. We offer everything from managed IT to audio visual and everything in between. Our dedicated staff will keep you informed on every project from beginning to end. Reach out to discuss your next IT project. ISR Technology is ready for the future and ready to help.`;
  const title = `ISR Technology`;

  const logos = [];
  certificationsData.map((el) => logos.push(el.link));

  return (
    <>
      <Carousel currentPos={currentPos} />
      <Wrapper>
        <Container
          display={`flex`}
          maxWidth={`768px`}
          classe={`about-container`}
        >
          <ContentArea classe={`about-content`}>
            <div className="text">
              <SectionH1 h1={title} />
              <div className="underline"></div>
              <p>
                <Markdown>{copy}</Markdown>
              </p>
            </div>
            <div className="certifications">
              <Slider logos={logos} />
              <p className="caption">certifications</p>
            </div>
          </ContentArea>
        </Container>
      </Wrapper>
      <Footer />
    </>
  );
}
